import React from 'react';

import clsx from 'clsx';
import { FunctionalIcon } from '../icon/Icon';
import { FormFieldProps, FormFieldScope } from './form-field.interface';

export const FormField: React.FC<FormFieldProps> = ({
  messageId,
  required,
  children,
  state = 'default',
  message,
  ...props
}) => {
  return (
    <fieldset
      className={clsx(FormFieldScope, 'zep-inline-flex', 'zep-flex-col', 'zep-flex-nowrap')}
      data-testid="zep-form-fieldset"
      {...props}
    >
      {children}
      <span
        id={messageId}
        className={clsx(
          { 'text-zep-error': state === 'error' || required, 'text-zep-info': state === 'info' },
          'gap-2 zep-mt-0.5  zep-flex zep-flex-row zep-items-center',
        )}
      >
        {(state === 'error' || required) && (
          <FunctionalIcon name="warning-triangle-filled" className="text-zep-error" />
        )}
        {state === 'info' && <FunctionalIcon name="info-circle-filled" className="text-zep-info" />}
        {message}
      </span>
    </fieldset>
  );
};
